// app/case-studies/[slug]/page.jsx
"use client";

import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import Image from "next/image";
import Link from "next/link";
import { motion } from "framer-motion";
import { caseStudies } from "../case-data";
import { ArrowLeft } from "lucide-react";

export default function CaseDetail({ params }) {
  // Next.js passes params in App Router dynamic route
  const { slug } = params || {};
  const [caseItem, setCaseItem] = useState(null);

  useEffect(() => {
    const found = caseStudies.find((c) => c.slug === slug);
    setCaseItem(found ?? null);
  }, [slug]);

  if (!caseItem) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <p className="text-muted-foreground">Case not found</p>
          <Link href="/case-studies"><a className="text-accent hover:underline">Back to cases</a></Link>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-background via-background-soft to-background-muted">
      <main className="max-w-5xl mx-auto px-6 py-16">
        <Link
  href="/case-studies"
  className="inline-flex items-center gap-2 text-accent mb-6"
>
  <ArrowLeft size={16} /> Back to Case Studies
</Link>


        <div className="grid lg:grid-cols-2 gap-8 items-start">
          <motion.div initial={{ opacity: 0, x: -10 }} whileInView={{ opacity: 1, x: 0 }} className="rounded-2xl overflow-hidden bg-card/60 border border-border">
            <div className="aspect-[4/3] relative">
              <Image src={caseItem.before} alt={`${caseItem.title} - before`} fill className="object-cover" />
            </div>
            <div className="aspect-[4/3] relative">
              <Image src={caseItem.after} alt={`${caseItem.title} - after`} fill className="object-cover" />
            </div>
          </motion.div>

          <motion.div initial={{ opacity: 0, x: 10 }} whileInView={{ opacity: 1, x: 0 }} className="">
            <h1 className="text-2xl font-bold text-foreground">{caseItem.title}</h1>
            <div className="text-sm text-muted-foreground mt-2">{caseItem.condition} • {caseItem.age} yrs • {caseItem.duration}</div>

            <div className="mt-6 space-y-6 text-sm text-muted-foreground">
              <div>
                <h4 className="font-semibold text-foreground mb-2">Presentation</h4>
                <p>{caseItem.details.presentation}</p>
              </div>

              <div>
                <h4 className="font-semibold text-foreground mb-2">Assessment</h4>
                <p>{caseItem.details.assessment}</p>
              </div>

              <div>
                <h4 className="font-semibold text-foreground mb-2">Treatment Plan</h4>
                <p>{caseItem.details.plan}</p>
              </div>

              <div>
                <h4 className="font-semibold text-foreground mb-2">Monitoring</h4>
                <p>{caseItem.details.monitoring}</p>
              </div>

              <div>
                <h4 className="font-semibold text-foreground mb-2">Outcome</h4>
                <p className="text-foreground">{caseItem.outcome}</p>
              </div>
            </div>

            <div className="mt-8">
              <Link href="/HairGrowth/register">
                <button className="px-5 py-3 rounded-xl bg-primary text-primary-foreground">Book Consultation</button>
              </Link>
            </div>
          </motion.div>
        </div>
      </main>
    </div>
  );
}
